/* App.css */

main {
  font-family: 'Arial', sans-serif;
  text-align: center;
  padding: 20px;
}

h1 {
  margin-bottom: 20px;
  color: #333;
}

.input-slider {
  width: 80%;
  margin: 10px auto;
}

.slider-output {
  font-size: 1.2rem;
  margin-bottom: 5px;
}

.output-section {
  margin-top: 20px;
  border: 1px solid #ccc;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 8px;
}

.output {
  font-size: 2rem;
  font-weight: bold;
  color: #ff5722; /* Orange color */
}

/* Colorful styles */
.input-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background-color: #ff5722; /* Orange color */
  border: 2px solid #fff;
  border-radius: 50%;
  cursor: pointer;
}

.input-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background-color: #ff5722; /* Orange color */
  border: 2px solid #fff;
  border-radius: 50%;
  cursor: pointer;
}

/* Change track color as well */
.input-slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  background-color: #ccc;
  border-radius: 5px;
}

.input-slider::-moz-range-track {
  width: 100%;
  height: 5px;
  background-color: #ccc;
  border-radius: 5px;
}
